<template>
  <div class="divbg">
    <div class="menustyle">
      <div class="menu-p">
        <p style="padding-top:15px;"></p>
      </div>
      <Menu style="z-index:4" mode="horizontal" theme="light" active-name="All">
        <MenuItem name="All"></MenuItem>
        <MenuItem name="audit "></MenuItem>
        <MenuItem name="pass"></MenuItem>
        <MenuItem name="nopass"></MenuItem>
        <Button
          type="primary"
          style="float:right;margin-top:1%;margin-right:1%"
          @click="toSuetail"
        ></Button>
      </Menu>
      <div>
        <div class="none">
          <div>
            <img src="../../serviceimg/noneimg/none.png" alt />
          </div>
          <p></p>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.none {
  width: 100%;
  height: 570px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 200px;
}
.none img {
  width: 400px;
  height: 400px;
  
}
.none p {
  
  float: right;
  font-size: 24px;
}
.menu-p {
  font-size: 180%;
  font-weight: 540;
  text-indent: 1em;
}
.divbg {
  background: rgb(238, 238, 238);
}
.menustyle {
  z-index: 3;
  width: 70%;
  height: 720px;
  margin: 1% auto;
  background: white;
}
</style>
<script>
export default {
  data() {
    return {};
  },
  mounted() {},
  methods: {
    toSuetail() {
      this.$router.push({ path: "/surrender/surrenderDetail" });
    }
  }
};
</script>


