<template>
  <div class="divbg">
    <div class="menustyle">
      <div class="menu-p">
        <p style="padding-top:15px;"></p>
      </div>
      <hr class="line" />
      <div class="formsty">
        <Form class="inputstyle" :rules="certificationValidate">
          <FormItem label=" " prop="companyName">
            <label class="fontlabel">:</label>
            <Input
              v-model="companyName"
              size="large"
              style="width:78%;float:right"
              placeholder=""
            />
            <div style="clear:both"></div>
          </FormItem>
          <FormItem label=" " prop="name">
            <label class="fontlabel">:</label>
            <Input v-model="proposer" size="large" style="width:78%;float:right" />
            <div style="clear:both"></div>
          </FormItem>
          <FormItem label=" " prop="tel">
            <label class="fontlabel">:</label>
            <Input v-model="telPhone" size="large" style="width:78%;float:right" />
            <div style="clear:both"></div>
          </FormItem>
        </Form>
        <!--  -->
        <input type="file" accept="image/*" @change="chooseImg" />
        <!--  -->
        <canvas ref="imgPreview" height="0" width="0"></canvas>
        <!--  -->
        <button @click="uploadImg"></button>
        <!-- url -->
        <img :src="imgUrlFromServer" />
      </div>
    </div>
  </div>
</template>
<style scoped>
.ivu-form-item-error-tip {
  padding-left: 20px;
}
.fontlabel {
  float: left;
  font-size: 16px;
}
.formsty {
  width: 60%;
  margin: 0 auto;
  margin-top: 5%;
  text-align: center;
}
.inputstyle {
  width: 80%;
}
.line {
  margin-top: 1%;
  color: #dcdee2;
  height: 1px;
  border: none;
  border-top: 1px solid;
}

.menu-p {
  font-size: 180%;
  font-weight: 540;
  text-indent: 1em;
}
.divbg {
  background: rgb(238, 238, 238);
}
.menustyle {
  z-index: 3;
  width: 70%;
  height: 720px;
  margin: 1% auto;
  background: white;
}
</style>

<script>
import axios from "axios";
export default {
  data() {
    return {
      companyName: "",
      proposer: "",
      telPhone: "",
      imgUrlFromServer: "",
      base64: "",
      files: "",
      certificationValidate: {
        
      }
    };
  },
  mounted() {},
  methods: {
    chooseImg(event) {
      let file = event.target.files[0];
      this.files = event.target.files[0];
      let reader = new FileReader();
      let img = new Image();
      // 
      reader.readAsDataURL(file);
      // 
      reader.onloadend = e => {
        img.src = e.target.result;
        // e.targetreader
        // console.log(reader.result)
        // reader.resultbase64
        this.base64 = reader.result;
      };
      // 
      let canvas = this.$refs["imgPreview"];
      let context = canvas.getContext("2d");
      img.onload = () => {
        img.width = 100;
        img.height = 100;
        // canvas
        canvas.width = 100;
        canvas.height = 100;
        // canvas
        context.clearRect(0, 0, 100, 100);
        // 
        context.drawImage(img, 0, 0, 100, 100);
      };
    },
    uploadImg() {
      let _this = this;
      let formData = new FormData();
      formData.append("file", _this.files);
      formData.append("companyName",_this.companyName);
      formData.append("proposer",_this.proposer);
      formData.append("telPhone",_this.telPhone);
      axios({
        url: "pushAduit",
        method: "post",
        data: formData
      }).then(res => {
        console.log(res.data);
        _this.imgUrlFromServer = _this.BASEIMG.BASEIMG_URL + res.data;
      });
    }
  }
  
};
</script>


