import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import zhiHuiYuanIndex from '@/components/zhiHuiYuanIndex'
import ZhiHuiYuanHome from '@/components/ZhiHuiYuanHome'
import dataView from '@/components/businessApplication/dataView'
import repairs from '@/components/businessApplication/repairs'
import expertservices from '@/components/businessApplication/expertservices'
import chuangxinsp from '@/components/businessApplication/chuangxinsp'
import eventorganizer from '@/components/businessApplication/eventorganizer'
import surrender from '@/components/businessApplication/surrender'
import chuangyefd from '@/components/businessApplication/chuangyefd'
import repairsDetail from '@/components/businessApplication/subappliction/repairsDetail'
import surrenderDetail from '@/components/businessApplication/subappliction/surrenderDetail'
import activeDetail from '@/components/businessApplication/subappliction/activeDetail'
import dangjian from '@/components/dangjian'
import qiyerenzheng from '@/components/businessApplication/qiyerenzheng'

import BaseImg from "@/constants/imgBasePath"
Vue.prototype.BASEIMG=BaseImg;
Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    {
      path: '/home',
      name: 'home',
      component: ZhiHuiYuanHome
    },
    {
      path: '/dataView',
      name: 'dataView',
      component: dataView
    },
    {
      path: '/',
      name: 'main',
      component: Main,
      children: [{
        path: '/',
        name: 'zhiHuiYuanIndex',
        meta: {
            hideInMenu: true,
            title: '',
            notCache: true
        },
        component:zhiHuiYuanIndex,
    },
    {
      path: '/dangjian',
      name: 'dangjian',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: dangjian
    },
    {
      path: '/qiyerenzheng',
      name: 'qiyerenzheng',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: qiyerenzheng
    },
    {
      path: '/chuangyefd',
      name: 'chuangyefd',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: chuangyefd
    },
    {
      path: '/chuangxinsp',
      name: 'chuangxinsp',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: chuangxinsp
    },
    {
      path: '/expertservices',
      name: 'expertservices',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: expertservices
    },
    
    {
      path: '/surrender',
      name: 'surrender',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: surrender
    },{
      path: '/surrender/surrenderDetail',
      name: 'surrenderDetail',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: surrenderDetail
    },
    
    
    {
      path: '/repairs',
      name: 'repairs',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: repairs
    },{
      path: '/repairs/repairsDetail',
      name: 'repairsDetail',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: repairsDetail
    },{
      path: '/eventorganizer',
      name: 'eventorganizer',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: eventorganizer
    },{
      path: '/eventorganizer/activeDetail',
      name: 'activeDetail',
      meta: {
        hideInMenu: true,
        title: '',
        notCache: true
    },
      component: activeDetail
    }]  
    }
  ]
})



