<script>
export default {
  data() {
    return {
      dataList: [
        require("../assets/yuanqu.jpg"),
        require("../assets/yuanqu2.jpg"),
        require("../assets/yuanqu3.jpg")
      ],
      currentIndex: 0, //
      timer: null, //
      showAll: false
    };
  },
  mounted() {
    this.runInv();
  },
  methods: {
    runInv() {
      this.timer = setInterval(() => {
        this.gotoPage(this.nextIndex);
      }, 2000);
    },
    gotoPage(index) {
      this.currentIndex = index;
    },
    isShowAll() {
      this.showAll = !this.showAll;
    },
    toadmin() {
      window.location.href = "http://47.92.1.31:8080/cainiao/";
    }
  },
  computed: {
    //
    prevIndex() {
      if (this.currentIndex == 0) {
        return this.dataList.length - 1;
      } else {
        return this.currentIndex - 1;
      }
    },
    //
    nextIndex() {
      if (this.currentIndex == this.dataList.length - 1) {
        return 0;
      } else {
        return this.currentIndex + 1;
      }
    }
  }
};
</script>


