<script>
export default {
  data() {
    return {
      dataList: [
        require("../assets/yuanqu.jpg"),
        require("../assets/yuanqu2.jpg"),
        require("../assets/yuanqu3.jpg")
      ],
      currentIndex: 0, //
      timer: null, //
      showAll: false
    };
  },
  mounted() {
    this.runInv();
  },
  methods: {
    runInv() {
      this.timer = setInterval(() => {
        this.gotoPage(this.nextIndex);
      }, 2000);
    },
    gotoPage(index) {
      this.currentIndex = index;
    },
    isShowAll() {
      this.showAll = !this.showAll;
    },
    toadmin() {
      window.location.href = "http://47.92.1.31:8080/cainiao/";
    },
    todangjian() {
      this.$store.commit("setact", "5");
      this.$router.push({ name: "dangjian" });
    },
    torepairs() {
      this.$store.commit("setact", "6-6");
      this.$router.push({ name: "repairs" });
    },
    toeventorganizer() {
      this.$store.commit("setact", "6-5");
      this.$router.push({ name: "eventorganizer" });
    },
    tosurrender() {
      this.$store.commit("setact", "6-7");
      this.$router.push({ name: "surrender" });
    },
    tochuangyefd() {
      this.$store.commit("setact", "2-8");
      this.$router.push({ name: "chuangyefd" });
    },
    todatacount() {
      let d = this.$router.resolve({ path: "/dataView" });

      window.open(d.href, "_blank");
    },
    toexpertservices(){
      this.$store.commit("setact", "2-7");
      this.$router.push({ name: "expertservices" });
    },
    tochuangxinsp(){
      this.$store.commit("setact", "2-5");
      this.$router.push({ name: "chuangxinsp" });
    }
  },
  computed: {
    //
    prevIndex() {
      if (this.currentIndex == 0) {
        return this.dataList.length - 1;
      } else {
        return this.currentIndex - 1;
      }
    },
    //
    nextIndex() {
      if (this.currentIndex == this.dataList.length - 1) {
        return 0;
      } else {
        return this.currentIndex + 1;
      }
    }
  }
};
</script>

