.layout {
  border: 1px solid #d7dde4;
  background: #ffffff;
  position: relative;
  border-radius: 4px;

  overflow: hidden;
}
.layoutstyle {
  background: #ffffff;
}
.bodydiv {
  width: 1290px;
  margin: 0 auto;
}
.layout-nav {
  margin-right: 20px;
}
.layout-footer-center {
  text-align: center;
}
.layout-footer-left {
  position: absolute;
  text-align: left;
}
.ulstyle {
  list-style: none;
  clear: both;
  display: inline-block;
}
.uldiv {

  margin: 0 auto;
}
.uldivbody {
  margin-top: 80px;
}

.listyle {
  cursor: pointer;
  border: 1px solid #d7dde4;
  border-radius: 4px;
  text-align: center;
  background: #e6e8eb;
  width: 280px;
  height: 280px;
  margin: 20px 20px;
  float: left;
  padding: 5px;
}
.listyle p {
  margin-top: 15px;
  font-size: 30px;
}
.djimgstyle {
  width: 70%;
  height: 70%;
}
.moreorless {
  cursor: pointer;
  width: 1240px;
  background: #e6e8eb;
  margin: 0 auto 20px auto;
  font-size: 20px;
}
.layout-nav span {
  font-size: 150%;
}
.theNews {
  clear: both;
  margin-left: 25px;
  font-size: 200%;
  text-align: center;
  float: left;
}
.divimg {
  float: left;
  margin-left: 25px;
}
.divimg img {
  width: 600px;
  height: 450px;
}
.newstext {
  float: right;
  margin-right: 10%;
  margin-top: 20px;
}
.newstitle {
  font-size: 25px;
}
.newsbody {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.45);
}
.newsd {
  width: 500px;
}
.newstext ul {
  border-top: 1px dashed darkgray;
  width: 500px;
  padding-top: 30px;
  margin-top: 30px;
}
.newstext li {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  font-size: 20px;
}
.newset {
  width: 70%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-family: PingFangSC-Medium;
  font-size: 20px;
  color: rgba(0, 0, 0, 0.65);
}
.newsdate {
  font-size: 16px;
  padding-top: 5px;
}

.titlebg {
  width: 100%;
}
.layout-footer {
  background: rgb(9, 9, 65);
  color: rgb(255, 255, 255);
}
.affixstyle {
  width: 180px;
  position: absolute;
  top: 50%;
  left: 90%;
}
.affixstyle img {
  width: 150px;
  height: 150px;
}
.affbox-cont {
  margin: 0 auto;
  text-align: center;
}
.affbox-num {
  background: #0173a3;
  color: white;
  border-radius: 6px;
  text-align: center;
}
.xiaozfm {
  width: 100%;
  margin-top: 2%;
}
.xiaozfm-body {
  text-align: center;
  margin: 0 auto;
}
.xiaozfm-ul {
  list-style: none;
  clear: both;
  display: inline-block;
}
.xiaozfm-li {
  cursor: pointer;
  text-align: center;

  float: left;
}
.xiaozfm-li img {
  margin-left: 20px;
  width: 300px;
  height: 190px;
}
.linkus img {
  width: 30px;
  float: left;
  margin-top: 25px;
}
