<template>
  <div class="layout">
    <Layout class="layoutstyle">
      <div class="titlebg">
        <div class="item">
          <img :src="dataList[currentIndex]" style="width:100%" />
        </div>
      </div>
      <!-- <div class="titlebg">
        <img src="../assets/yuanqu.jpg" style="width:100%" alt />
      </div>-->
      <div class="bodydiv">
