                    <p></p>
                  </li>
                  <li class="listyle">
                    <img class="djimgstyle" src="../assets/dangjianimg/tpbm.png" />
                    <p></p>
                  </li>
                  <li class="listyle">
                    <img class="djimgstyle" src="../assets/dangjianimg/approval.png" />
                    <p></p>
                  </li>
                </ul>

                <ul class="ulstyle">
                  <li class="listyle">
                    <img class="djimgstyle" src="../assets/dangjianimg/integral.png" />
                    <p></p>
                  </li>
                  <li class="listyle">
                    <img class="djimgstyle" src="../assets/dangjianimg/upload.png" />
                    <p></p>
