<template>
  <div class="divbg">
    <div class="menustyle">
      <div class="menu-p">
        <p style="padding-top:15px;"></p>
      </div>
      <hr class="line" />
      <div class="formsty">
        <Form class="inputstyle" :rules="repairsValidate">
          <FormItem label=" " prop="company">
            <label class="fontlabel">:</label>
            <Input size="large" style="width:78%;float:right" placeholder="" />
            <div style="clear:both"></div>
          </FormItem>
          <FormItem label=" " prop="address">
            <label class="fontlabel">:</label>
            <Input size="large" style="width:78%;float:right" placeholder="" />
            <div style="clear:both"></div>
          </FormItem>
          <FormItem label=" " prop="area">
            <label class="fontlabel">:</label>
            <Input size="large" style="width:78%;float:right" />
            <div style="clear:both"></div>
          </FormItem>
          <FormItem label=" " prop="problem">
            <label class="fontlabel">:</label>
            <Input size="large" style="width:78%;float:right" type="textarea" :rows="6" placeholder="" />
          </FormItem>
          <FormItem>
            <Button type="primary"></Button>
          </FormItem>
        </Form>
      </div>
    </div>
  </div>
</template>
<style scoped>
.ivu-form-item-error-tip{
  padding-left: 20px;
}
.fontlabel {
  float: left;
  font-size: 16px;
}
.formsty {
  width: 60%;
  margin: 0 auto;
  margin-top: 5%;
  text-align: center;
}
.inputstyle {
  width: 80%;
  
}
.line {
  margin-top: 1%;
  color: #dcdee2;
  height: 1px;
  border: none;
  border-top: 1px solid;
}

.menu-p {
  font-size: 180%;
  font-weight: 540;
  text-indent: 1em;
}
.divbg {
  background: rgb(238, 238, 238);
}
.menustyle {
  z-index: 3;
  width: 70%;
  height: 720px;
  margin: 1% auto;
  background: white;
}
</style>
<script>
export default {
  data() {
    return {
      repairsValidate: {
        company: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        area: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
        ],
      }
    };
  }
};
</script>


